import React, { useState, useEffect } from 'react';
import { Row, Col, Card, message, Button } from 'antd';
import 'antd/dist/antd.css';
import axios from './axios';
import StompClient from './StompClient';
function Home() {
  const [machieInfo, setMachieInfo] = useState();
  const [subMachiInfo, setSubMachiInfo] = useState(false);
  useEffect(() => {
    // ws
    StompClient.connect();

    console.log(StompClient)
    let timer
    if (!subMachiInfo) {
      setSubMachiInfo(true)
      timer = setTimeout(() => {
        StompClient.subscribe('/user/topic/machine/info', (msg) => {
          console.log(msg);
          let machie = JSON.parse(msg.body);
          let ms = Object.assign({}, machieInfo);
          ms[machie.macAddress] = machie
          console.log(ms)
          setMachieInfo(ms)
        });
      }, 2000)
    }
    return function cleanup() {
      clearTimeout(timer)
    }

  }, [])
  let machieUi = []
  if (machieInfo) {
    Object.keys(machieInfo).map(key => {
      let value = machieInfo[key];

      machieUi.push(
        <Col span={8}> <Card title={value.hostName} bordered={false}>
          <Row>
            <div> <span>cpu:&nbsp;&nbsp;&nbsp;{value.cpu.toFixed(2)}%</span>          </div>
          </Row>
          <Row>
            <div><span>:&nbsp;&nbsp;&nbsp;{value.userMemory}MB/{value.totalMemory}MB</span></div>
          </Row>
          <Row>
            <div><span>:&nbsp;&nbsp;&nbsp;{value.time}</span></div>
          </Row>
          <Row>
            <Button onClick={()=>{
              StompClient.sendMsg("/user/sendUser/shutDown",{},JSON.stringify({"macAddress":key}))
            }} type='primary' style={{width:'50%'}}></Button>
          </Row>
        </Card>
        </Col>
      )
    });

  }
  return (
    <div className="site-card-wrapper">
      <Row gutter={16}>
        <Col span={8}>
          <Card title="hello" bordered={false}>
            <span></span>
            <a href="/logout/cas"></a>
          </Card>
        </Col>
        {machieUi}

      </Row>
    </div>
  )
}
export default Home;
